$width-desktop: 1280px;
$header-bg-color: #59ab6e;
$main-bg-color: #f7f8fc;
$footer-bg-color: #1d242d;
$text-color: #212934;
$footer-text-color: #cfd6e1;
$white: #ffffff;
$border-card-color: lighten($header-bg-color, 30%);
$border-card-color-basked: darken($header-bg-color, 10%);

@import "./styles/header";
@import "./styles/main";
@import "./styles/footer";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  display: block;
}

html {
  height: 100%;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 300;
}

body {
  height: 100%;
  display: flex;
  flex-direction: column;
  color: $text-color;
}

.wrapper {
  max-width: $width-desktop;
  margin: 0 auto;
  padding: 0 30px;
  //border: 1px dotted #c91016;
}
