.footer {
  color: $footer-text-color;
  background-color: $footer-bg-color;
  padding: 20px 0;

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.github {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.github__logo {
  width: 50px;
  height: 50px;
  display: inline-block;
  background: url("./assets/png/github-50.png") no-repeat;
}

.github__name {
  margin-left: 10px;
  color: $footer-text-color;
}

.footer__year {
  font-size: 2rem;
}

.footer__rs {
  width: 100px;
  height: 37px;
  display: flex;
  background: url("./assets/svg/rs_school_js.svg") no-repeat right;
}
