.header {
  background-color: $header-bg-color;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
  color: $white;

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header__brand {
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
}

.header__logo {
  width: 150px;
  height: 150px;
  background: url("./assets/png/logo.png") no-repeat center/150px;
}

.header__title {
  font-size: 3rem;
}

.header__basket {
  width: 64px;
  height: 64px;
  position: relative;
  background: url("./assets/svg/basket.svg") no-repeat;
  cursor: pointer;
}

.basket__quantity {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -10px;
  padding: 3px;
  border: 3px solid rgb(234, 205, 205);
  border-radius: 50%;
  background-color: #ff0202;
  right: -20px;
}
