.main {
  flex: 1 0 auto;
  background-color: $main-bg-color;

  .wrapper {
    height: 100%;
    display: flex;
  }
}

.main__filters {
  min-width: 260px;
  width: 30%;
  height: 100%;
  padding: 20px;
  background: $white;
  box-shadow: 0 3px 5px 2px rgba(167, 175, 182, 0.15);
}

/* Search */
.search-label {
  position: relative;

  .search-input {
    width: 100%;
    margin-top: 20px;
    padding: 5px 30px 5px 5px;
    border: 2px solid #bdbdbd;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    font-family: inherit;

    &:focus {
      outline: none;
      color: #212529;
      background-color: #fff;
      border-color: $header-bg-color;
      outline: 0;
      box-shadow: 0 0 0 0.1rem rgba($header-bg-color, 0.25);
    }

    &::placeholder {
      color: #212529;
      opacity: 0.4;
    }

    &::-webkit-search-cancel-button {
      display: none;
    }
  }

  .search-cleanup-button {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(./assets/png/remove.png) no-repeat center/20px;
    position: absolute;
    top: 1px;
    right: 5px;
    cursor: pointer;
    visibility: hidden;
  }
}

.main__cards {
  width: 100%;
  display: flex;
  row-gap: 30px;
  column-gap: 20px;
  padding: 20px 0 20px 20px;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-content: flex-start;
}

.card {
  height: fit-content;
  min-width: 290px;
  flex-basis: 30%;
  font-size: 0.75rem;
  padding: 20px;
  background: $white;
  border: 3px dashed $border-card-color;
  box-shadow: 0 0 0 1px $border-card-color, inset 0 0 0 1px $border-card-color;

  .card__name {
    text-align: center;
  }

  .card__img {
    min-height: 170px;
    position: relative;
    margin-top: 15px;

    .card__popular {
      width: 50px;
      height: 50px;
      position: absolute;
      z-index: 9;
      visibility: hidden;
      top: 5px;
      right: 30px;
      background: #e3e3e3 url(./assets/png/flame.png) no-repeat center/32px;
      border-radius: 50%;
      border: 3px solid #f77878;
    }

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .card__info {
    margin-top: 15px;

    .info-list {
      .info-list__item {
        list-style: none;
        margin-bottom: 5px;
      }
    }
  }
}

[data-basket = true] {
  border-color: $border-card-color-basked;
  box-shadow: 0 0 0 1px $border-card-color-basked, inset 0 0 0 1px $border-card-color-basked;
}

.card__basket {
  backface-visibility: hidden;
  background-color: $header-bg-color;
  border-radius: 6px;
  border-width: 0;
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .1) 0 2px 5px 0, rgba(0, 0, 0, .07) 0 1px 1px 0;
  color: #fff;
  cursor: pointer;
  font-size: 1rem;
  height: 44px;
  line-height: 1.15;
  margin: 12px 0 0;
  outline: none;
  overflow: hidden;
  padding: 0 25px;
  position: relative;
  text-align: center;
  text-transform: none;
  transition: all .2s, box-shadow .08s ease-in;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 100%;

  &:focus {
    box-shadow: rgba(77, 93, 50, 0.1) 0 0 0 1px inset, rgba(76, 93, 50, 0.2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(139, 211, 50, 0.3) 0 0 0 4px;
  }
}

.bold {
  font-weight: bold;
}

/* Modal window */
.modal {
  width: 300px;
  padding: 30px;
  position: fixed;
  z-index: 999;
  top: -15%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: $main-bg-color;
  transition: all 0.5s;
  border-bottom: 3px solid $header-bg-color;
  border-radius: 0 0 5px 5px;

  .modal__message {
    text-align: center;
  }

  .modal__button-close {
    @extend .card__basket;
    margin-top: 15px;
  }
}

.modal__back-layer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background: transparent;
}

/* Filters */
.filters {
  margin-top: 20px;
}

.filters__cpu-filter {
  margin-top: 20px;
  margin-bottom: 20px;

  ul {
    margin-top: 20px;
    display: flex;
    gap: 10px;

    li {
      list-style: none;
      padding: 5px;
      border: 2px solid $header-bg-color;
      border-radius: 10%;
      cursor: pointer;

      &:hover {
        @extend .filters-active
      }
    }
  }
}

.filters-active {
  background: $header-bg-color;
  color: #ffffff;
}

/* Reset buttons */
.reset-buttons {
  margin-top: 10px;
  button {
    @extend .card__basket
  }
}

/* Popular filter */
.filters__popular {
  input {
    margin-right: 5px;
  }
}
